@font-face {
  font-family: "Muli";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Muli Regular"), local("Muli-Regular"),
    url(https://fonts.gstatic.com/s/muli/v16/7Auwp_0qiz-afTLGKA.ttf)
      format("truetype");
}
@font-face {
  font-family: "Muli";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Muli Bold"), local("Muli-Bold"),
    url(https://fonts.gstatic.com/s/muli/v16/7Au_p_0qiz-adYnjOCX2yg.ttf)
      format("truetype");
}
#contactbody {
  margin: 0;
  color: #19345e;
  background-color: white;
}
#contactrow {
  margin: 0;
}
#contactsamping {
  /* background-image: url("../../assets/img/charles-rRWiVQzLm7k-unsplash.jpg"); */
  background-image: url("../img/charles-rRWiVQzLm7k-unsplash.jpg");
  background-size: 512px 792px;
  background-repeat: no-repeat;
  display: table;
}
.boxed {
  height: 792px;
  width: 512px;
  background-color: rgba(25, 52, 94, 0.65);
  border: #707070 1px solid;
  display: table-cell;
  text-align: center;
  vertical-align: middle;
}
#contactimg {
  opacity: 1;
  margin: 0 auto;
}
.contact-form {
  background-color: #ffffff;
}
formcontact {
  padding: 29px 130px;
  font-size: 23px;
  font-family: "Muli", sans-serif;
}
form label {
  font-size: 20px;
}
.form-group {
  font-size: 20px;
}
.submit {
  border-color: black;
}
#contactbtn {
  background-color: #f47522;
  border: #f47522 solid 2px;
  border-radius: 20px;
  padding: 5px 50px;
  font-size: 16px;
}
#contactbtn:hover,
.btn:hover {
  background-color: #ffffff;
  color: #f47522;
  border: #f47522 solid 2px;
}
#nat {
  -webkit-appearance: none;
  background-color: #ffffff;
  /* background-image: url("../img/down-arrow.png"); */
  background-repeat: no-repeat;
  background-position: 95% center;
  background-size: 22px;
}
span.color {
  color: red;
}
.form-control {
  height: 60px;
  width: 460px;
}
.form-control:focus {
  border: #19345e 2px solid;
  box-shadow: none;
}
.form-control:active {
  border: #f47522 2px solid;
}
.pesan {
  height: 120px;
}
@media (max-width: 991px) {
  form,
  .form-control,
  .contact-form {
    width: 100%;
    padding: 5px;
    margin: 0;
  }
}
@media (max-width: 767px) {
  form,
  .form-control,
  .contact-form {
    width: 100%;
    padding: 0;
    margin: 0;
  }
}
#homebody {
  background-image: url("../img/bg-img.png");
  background-size: cover;
  background-repeat: no-repeat;
  font-family: "Roboto", sans-serif;
  color: #19345e;
}
header {
  background-color: white;
}
header a {
  padding: 0px;
  margin: 0px;
  color: #19345e;
}
header .header-logo {
  margin: 11px 0px 11.73px 0px;
}
header .header-menu {
  align-items: center;
  display: table;
  text-align: right;
  font-family: Muli, sans-serif;
  font-size: 13px;
  padding: 0px;
}
header .header-menu ul {
  font-size: 13px;
  text-decoration: none;
}
header .header-menu ul li {
  display: table-cell;
  vertical-align: middle;
  height: 100%;
  padding: 34.5px 10px 34.4px 10px;
}
header .header-menu ul li a {
  padding: 0px;
  margin: 0px;
  display: table;
}
#now {
  color: #f47522;
  font-weight: 400;
}
.profpic img {
  height: 355px;
  width: 355px;
  border: white solid 1px;
  border-radius: 50%;
}
.content {
  padding: 94px;
}
h1 {
  font-weight: bold;
  font-size: 85px;
}
h2 {
  font-weight: medium;
  font-size: 35px;
}
h3 {
  font-weight: medium;
  font-size: 25px;
}
.bio {
  padding: 55px 0px 0px 75px;
  vertical-align: middle;
}
.btn {
  -webkit-appearance: none;
  background-color: #f47522;
  border: #f47522 solid 2px;
  color: white;
  margin-top: 20px;
  font-size: 16px;
  border-radius: 20px;
  padding: 5px 25px;
}
a:hover {
  color: #f47522;
  text-decoration: none;
}
@media (max-width: 767px) {
  .content {
    width: 100%;
    margin: 0;
  }
  .bio {
    padding: 0;
    margin: 20px;
  }
  .profpic img {
    height: 200px;
    width: 200px;
    margin: 20px;
  }
}
@media (max-width: 992px) {
  .content {
    width: 100%;
    margin: 10px;
    padding: 0;
  }
  .profpic img {
    height: 250px;
    width: 250px;
    margin: 70px 0px 0px 0px;
  }
}
.about-me {
  color: #19345e;
  background-color: #f4f7fc;
  vertical-align: middle;
  display: table;
  font-size: 40px;
  font-family: Roboto, sans-serif;
  padding: 43px 0px 42px 0px;
  font-weight: bold;
}
.dot-dot {
  position: relative;
  right: 120px;
  bottom: 50px;
}
.hello-me {
  height: 100%;
  width: 100%;
  font-family: Roboto, sans-serif;
  font-size: 17px;
  color: #19345e;
}
footer {
  background-color: #19345e;
  color: white;
  font-size: 14px;
  padding: 33.45px 0px 33.45px 0px;
}
.table-skill {
  font-size: 17px;
  color: #19345e;
}
.table-skill table {
  width: 130%;
  padding: 4px;
  font-size: 14px;
}
.table-skill table td {
  border: 1px solid #707070;
}
.aku {
  padding: 51px 63px 30px 29px;
}
@media (max-width: 767px) {
  .table-skill {
    margin-bottom: 20px;
    overflow: scroll;
  }
  .header-menu {
    text-align: right;
    float: none;
  }
  .header-logo {
    text-align: center;
  }
}
